'use client'

const HowItWorks = ({ number, title, children }) => {
    return (
        <section className="max-w-7xl mx-auto px-6 py-20">
            <h2 className="text-3xl font-semibold text-center mb-14">
                How Stampi Works
            </h2>

            <div className="grid md:grid-cols-3 gap-10 text-center">
                <Step number="1" title="Customer Pays">
                    Customer pays via UPI or cash and scans your QR.
                </Step>
                <Step number="2" title="Stamp Added">
                    Stampi verifies payment and adds a digital stamp.
                </Step>
                <Step number="3" title="Reward Earned">
                    Customer redeems rewards and comes back again.
                </Step>
            </div>
        </section>
    )
}

export default HowItWorks

const Step = ({ number, title, children }) => {
    return (
        <div>
            <div className="w-12 h-12 mx-auto mb-4 rounded-full bg-primary text-white flex items-center justify-center font-bold">
                {number}
            </div>
            <h3 className="font-semibold mb-2">{title}</h3>
            <p className="text-gray-600 text-sm">{children}</p>
        </div>
    )
}